.team {
  font-size: 35px;
  text-align: center;
  font-weight: 600;
  padding-top: 4%;
  margin-bottom: 2%;
}

.team-p {
  width: 50%;
  display: flex;
  justify-content: center;
  text-align: center;
  margin: auto;
}

.position {
  font-weight: bold;
}

h3 {
  font-weight: bolder;
}

.discription {
  width: 50%;
  text-align: center;
  display: flex;
  justify-content: center;
  position: relative;
  left: 25%;
}

li {
  list-style: none;
}


/* style member component */
.list-members {
  background: #51FFFD;
  width: 100%;
  height: 80%;
  /* margin: 4% auto; */
  margin-top: 5%;
  display: flex;
  flex-wrap: wrap;

  @media screen and (max-width: 960px) {
    width: 100%;
  }
}

img {
  border-radius: 0;
}

.member {
  flex-basis: 50%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.member-image {
  width: 50%;
  height: 100%;
  cursor: pointer;
  overflow: hidden;
  position: relative;

  @media screen and (max-width: 960px) {
    width: 60%
  }
}

.member-image img {
  width: 100%;
  height: 100%;
  transition: 1s;
}

/* .member-image:hover img {
  transform: scale(1.1);
} */

.member-info {
  width: 50%;
  text-align: center;
}

.member-info p {
  margin: 20px 0;
}


/* style social link */
.social-link .fab {
  width: 35px;
  height: 35px;
  /* line-height: 35px; */
  /* border: 1px solid #000; */
  margin: 0 7px;
  cursor: pointer;
  transition: transform .5s;
  margin-top: 85px;
}

.social-link .fab:hover {
  /* background: #000;
  color: #ffe501; */
  transform: translateY(-7px);
}

